import React, { Component } from 'react'
import { Form, Row, Col } from 'react-bootstrap'

export default class Contact extends Component {
    render() {
        return (
            <Form>
                <h4>Contact Me</h4>
                <br />
                <Form.Group controlId="formGroupEmail">
                    <Form.Control className="contact-field" type="text" placeholder="Your name" />
                </Form.Group>
                <Form.Group controlId="formGroupEmail">
                    <Form.Control className="contact-field" type="email" placeholder="Email address" />
                </Form.Group>
                <Form.Group controlId="formGroupEmail">
                    <Form.Control className="contact-field" type="text" placeholder="Subject" />
                </Form.Group>
                <Form.Group controlId="formGroupEmail">
                    <Form.Control className="contact-field" as="textarea" placeholder="Message" rows={4} />
                </Form.Group>
            </Form>
        )
    }
}
